import React, { useState, useEffect } from 'react';
import { Container, Card, CardContent, Button, Typography } from '@mui/material';
import { db } from '../db/db';

export default function Parametre() {
    const [status, setStatus] = useState('');
    const [observatoireCount, setObservatoireCount] = useState(0);
    const [speciesCount, setSpeciesCount] = useState(0);

    useEffect(() => {
        fetchCounts();
    }, []);

    async function fetchCounts() {
        const observatoireArray = await db.observatoire.toArray();
        setObservatoireCount(observatoireArray.length);
        const speciesArray = await db.species.toArray();
        setSpeciesCount(speciesArray.length);
    }

    async function handleUpdateObservatoireApiData() {
        try {
            await db.observatoire.clear();
            setStatus('Base de donnes des observatoires vide');

            const response = await fetch('https://www.lannobs.langazel.asso.fr/api/v1/aggregator');
            const data = await response.json();

            const observatoireData = Object.values(data).map((item) => ({
                titre: item.titre,
                metakey: item.metakey,
                icon: item.icon,
                couleur: item.couleur,
                nomvar: item.nomvar,
                nom: item.nom,
                nomdeux: item.nomdeux,
                latin: item.latin,
                indice: item.indice,
                saisie: item.saisie,
                categorie: item.categorie,
                description: item.description
            }));

            await db.observatoire.bulkAdd(observatoireData);
            setStatus('Donnes des observatoires API charges avec succs');
            fetchCounts();
        } catch (error) {
            setStatus(`Erreur lors de la mise  jour des donnes de l'API : ${error}`);
        }
    }

    async function handleUpdateSpeciesData() {
        try {
            await db.species.clear();
            setStatus('Base de donnes des espces vide');

            const response = await fetch('https://www.lannobs.langazel.asso.fr/api/v1/especes');
            const data = await response.json();

            const speciesData = Object.values(data).map((item) => ({
                cdnom: item.cdnom,
                nom: item.nom,
                nomvern: item.nomvern,
                observatoire: item.observatoire, // Corrected typo here
                rang: item.rang
            }));

            await db.species.bulkAdd(speciesData);
            setStatus('Donnes des espces charges avec succs');
            fetchCounts();
        } catch (error) {
            setStatus(`Erreur lors de la mise  jour des donnes de l'API : ${error}`);
        }
    }

    async function handleUpdateParamData() {
        try {
            await db.etude.clear();
            await db.protocole.clear();
            await db.orga.clear();
            setStatus('Bases de donnes vides');

            const response = await fetch('https://www.lannobs.langazel.asso.fr/api/v1/param');
            const data = await response.json();

            const protocoleData = data.protocole.map((item) => ({
                idprotocole: item.idprotocole,
                protocole: item.protocole
            }));

            const organismeData = data.organisme.map((item) => ({
                idorg: item.idorg,
                organisme: item.organisme
            }));

            const etudeData = data.etude.map((item) => ({
                idetude: item.idetude,
                etude: item.etude
            }));

            await db.protocole.bulkAdd(protocoleData);
            await db.orga.bulkAdd(organismeData);
            await db.etude.bulkAdd(etudeData);

            setStatus('Donnes des paramtres API charges avec succs');
            fetchCounts();
        } catch (error) {
            setStatus(`Erreur lors de la mise  jour des donnes de l'API : ${error}`);
        }
    }

    return (
        <Container>
            <Card>
                <CardContent>
                    <Typography variant="h5">Observatoires</Typography>
                    <Button onClick={handleUpdateObservatoireApiData} size="small">Mettre  jour les donnes des observatoires</Button>
                    <Typography variant="body1">Nombre d'observatoires : {observatoireCount}</Typography>
                </CardContent>
            </Card>
            <Card>
                <CardContent>
                    <Typography variant="h5">Espces</Typography>
                    <Button onClick={handleUpdateSpeciesData} size="small">Mettre  jour la liste des espces</Button>
                    <Typography variant="body1">Nombre d'espces : {speciesCount}</Typography>
                </CardContent>
            </Card>
            <Card>
                <CardContent>
                    <Typography variant="h5">Paramtres</Typography>
                    <Button onClick={handleUpdateParamData} size="small">Mettre  jour les paramtres</Button>
                </CardContent>
            </Card>
            <div>{status}</div>
        </Container>
    );
}
