import React, { useEffect, useState, useRef } from 'react';
import Card from '@mui/material/Card';
import { MapContainer, TileLayer, Marker, Popup, useMapEvents, LayersControl } from 'react-leaflet';
import 'leaflet/dist/leaflet.css';
import { Button, Typography, Box, Container, TextField, Switch, FormControl, InputLabel, Select, MenuItem } from '@mui/material';
import { db } from '../db/db';

const { BaseLayer } = LayersControl;

export default function Saisie() {
    const mapRef = useRef(null);
    const [initialCenterSet, setInitialCenterSet] = useState(false);
    const [clickedPosition, setClickedPosition] = useState(null);
    const userPositionRef = useRef(null);
    const [mapCenter, setMapCenter] = useState([48.505, -4.29]);
    const [showForm, setShowForm] = useState(false);
    const [organismes, setOrganismes] = useState([]);
    const [etudes, setEtudes] = useState([]);
    const [status, setStatus] = useState('');
    const [formData, setFormData] = useState({
        date1: new Date().toISOString().split('T')[0],
        date2: '',
        organisme: '',
        etude: '',
        typeDonnee: 'Pu',
        diffusion: 'Point'
    });

    // Fetch organismes and etudes from database
    useEffect(() => {
        async function fetchData() {
            const organismeData = await db.orga.toArray();
            const etudeData = await db.etude.toArray();
            setOrganismes(organismeData);
            setEtudes(etudeData);
        }
        fetchData();
    }, []);

    // Handler for user location
    function LocationUser() {
        const map = useMapEvents({
            locationfound(e) {
                if (!userPositionRef.current) {
                    userPositionRef.current = e.latlng;
                    setMapCenter(e.latlng);
                    if (!initialCenterSet) {
                        map.flyTo(e.latlng, map.getZoom());
                        setInitialCenterSet(true);
                    }
                }
            },
        });

        useEffect(() => {
            map.locate();
        }, [map]);

        return userPositionRef.current ? (
            <Marker position={userPositionRef.current}>
                <Popup>You are here</Popup>
            </Marker>
        ) : null;
    }

    // Handler for clicked position
    function LocationMarker() {
        useMapEvents({
            click(e) {
                setClickedPosition(e.latlng);
            },
        });

        return clickedPosition ? (
            <Marker position={clickedPosition}>
                <Popup>
                    You clicked at {clickedPosition.lat.toFixed(6)}, {clickedPosition.lng.toFixed(6)}
                </Popup>
            </Marker>
        ) : null;
    }

    const handleValidateClick = async () => {
        if (clickedPosition) {
            setShowForm(true);
        }
    };

    const handleFormSubmit = async (event) => {
        event.preventDefault();
        // Save form data to the database
        try {
            await db.observations.add({
                coordinates: clickedPosition,
                ...formData
            });
            console.log('Observation saved:', formData);
            setStatus('Observation enregistre avec succs');
            // Reset form and state
            setClickedPosition(null);
            setShowForm(false);
            setFormData({
                date1: new Date().toISOString().split('T')[0],
                date2: '',
                organisme: '',
                etude: '',
                typeDonnee: 'Pu',
                diffusion: 'Point'
            });
        } catch (error) {
            console.error('Failed to save observation:', error);
            setStatus('Erreur lors de l\'enregistrement de l\'observation');
        }
    };

    return (
        <Container>
            {!showForm ? (
                <Card sx={{ height: "auto", width: "100%" }}>
                    <MapContainer
                        center={mapCenter}
                        zoom={15}
                        scrollWheelZoom={true}
                        style={{ height: "450px", width: "100%" }}
                        whenCreated={mapInstance => { mapRef.current = mapInstance; }}
                    >
                        <LayersControl position="topright">
                            <BaseLayer checked name="OpenStreetMap Topographic">
                                <TileLayer
                                    attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
                                    url="https://{s}.tile.opentopomap.org/{z}/{x}/{y}.png"
                                />
                            </BaseLayer>
                            <BaseLayer name="Ortho Imagery">
                                <TileLayer
                                    attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
                                    url="https://data.geopf.fr/tms/1.0.0/ORTHOIMAGERY.ORTHOPHOTOS/{z}/{x}/{y}.jpeg"
                                />
                            </BaseLayer>
                        </LayersControl>
                        <LocationMarker />
                        <LocationUser />
                    </MapContainer>

                    {clickedPosition && (
                        <Box sx={{ padding: 2 }}>
                            <Typography variant="body1">
                                Coordinates: {clickedPosition.lat.toFixed(6)}, {clickedPosition.lng.toFixed(6)}
                            </Typography>
                            <Button variant="contained" color="primary" onClick={handleValidateClick}>
                                Valider
                            </Button>
                        </Box>
                    )}
                </Card>
            ) : (
                <Card sx={{ padding: 2 }}>
                    <Typography variant="h5">Saisie de la fiche d'observation</Typography>
                    <Typography variant="body1">Coordinates: {clickedPosition.lat.toFixed(6)}, {clickedPosition.lng.toFixed(6)}</Typography>
                    <form onSubmit={handleFormSubmit}>
                        <TextField
                            label="Date 1"
                            type="date"
                            value={formData.date1}
                            onChange={(e) => setFormData({ ...formData, date1: e.target.value })}
                            InputLabelProps={{ shrink: true }}
                            fullWidth
                            margin="normal"
                        />
                        <TextField
                            label="Date 2"
                            type="date"
                            value={formData.date2}
                            onChange={(e) => setFormData({ ...formData, date2: e.target.value })}
                            InputLabelProps={{ shrink: true }}
                            fullWidth
                            margin="normal"
                        />
                        <FormControl fullWidth margin="normal">
                            <InputLabel>Organisme</InputLabel>
                            <Select
                                value={formData.organisme}
                                onChange={(e) => setFormData({ ...formData, organisme: e.target.value })}
                            >
                                {organismes.map((org) => (
                                    <MenuItem key={org.idorg} value={org.idorg}>{org.organisme}</MenuItem>
                                ))}
                            </Select>
                        </FormControl>
                        <FormControl fullWidth margin="normal">
                            <InputLabel>Etude</InputLabel>
                            <Select
                                value={formData.etude}
                                onChange={(e) => setFormData({ ...formData, etude: e.target.value })}
                            >
                                {etudes.map((et) => (
                                    <MenuItem key={et.idetude} value={et.idetude}>{et.etude}</MenuItem>
                                ))}
                            </Select>
                        </FormControl>
                        <FormControl margin="normal">
                            <Typography>Type de donne</Typography>
                            <Switch
                                checked={formData.typeDonnee === 'Pr'}
                                onChange={(e) => setFormData({ ...formData, typeDonnee: e.target.checked ? 'Pr' : 'Pu' })}
                            />
                            <Typography>{formData.typeDonnee === 'Pr' ? 'Priv' : 'Public'}</Typography>
                        </FormControl>
                        <FormControl fullWidth margin="normal">
                            <InputLabel>Diffusion</InputLabel>
                            <Select
                                value={formData.diffusion}
                                onChange={(e) => setFormData({ ...formData, diffusion: e.target.value })}
                            >
                                <MenuItem value="Point">Point</MenuItem>
                                <MenuItem value="Maille">Maille</MenuItem>
                                <MenuItem value="Commune">Commune</MenuItem>
                                <MenuItem value="Dpartement">Dpartement</MenuItem>
                            </Select>
                        </FormControl>
                        <Box sx={{ display: 'flex', justifyContent: 'space-between', marginTop: 2 }}>
                            <Button variant="outlined" color="primary" onClick={() => setShowForm(false)}>
                                Revenir  la carte
                            </Button>
                            <Button variant="contained" color="primary" type="submit">
                                Suivant
                            </Button>
                        </Box>
                    </form>
                </Card>
            )}
        </Container>
    );
}
